import { WatchlistDataTablePriceCell } from "../../WatchlistDataTableCells/WatchlistDataTablePriceCell"
import { genericColumn } from "../../genericColumn"

function cashConversionCycleTtm(tableName: string) {
  const title = "Cash Conversion Cycle (LTM)"
  return genericColumn({
    endpoint: "companyKeyMetrics",
    columnKeys: ["cashConversionCycleTtm"],
    size: 110,
    title,
    enableSorting: true,
    enableHiding: true,
    tooltip: "Time to turn inputs into cash.",
    cell: info => {
      return (
        <WatchlistDataTablePriceCell
          tooltip={`Explain the ${title}`}
          queryResult={info.row.original.financialData.companyKeyMetrics}
          onClick={() => {
            info.row.original.onChatWithCell?.({
              prompt: `What has ${info.row.original.entityInfo.name} recently reported about cash conversion cycle or other operational efficiency metrics?`,
              value:
                info.row.original.financialData.companyKeyMetrics?.data?.cashConversionCycleTtm,
              entityInfo: info.row.original.entityInfo,
              columnName: `${tableName}: ${title}`,
            })
          }}
          value={info.row.original.financialData.companyKeyMetrics?.data?.cashConversionCycleTtm}
        />
      )
    },
  })
}

export { cashConversionCycleTtm }
